/* eslint-disable jsx-a11y/anchor-is-valid */
import { useState} from 'react'
import clsx from 'clsx'
import {
  toAbsoluteUrl,
  defaultMessages,
  defaultUserInfos,
  MessageModel,
  UserInfoModel,
  messageFromClient,
} from '../../helpers'
import ReactScrollableFeed from 'react-scrollable-feed'

type PersonProps = {
  userChat : {
      name : string 
      id : number
  }
  
}
const bufferMessages = defaultMessages

const ChatInner = (props: PersonProps) => {
  const {userChat} = props;
  const [chatUpdateFlag, toggleChatUpdateFlat] = useState<boolean>(false)
  const [message, setMessage] = useState<string>('')
  const [messages, setMessages] = useState<MessageModel[]>(bufferMessages)
  const [userInfos] = useState<UserInfoModel[]>(defaultUserInfos)

  const sendMessage = () => {
    const newMessage: MessageModel = {
      user: 2,
      type: 'out',
      text: message,
      time: 'Just now',
    }

    bufferMessages.push(newMessage)
    setMessages(bufferMessages)
    toggleChatUpdateFlat(!chatUpdateFlag)
    setMessage('')
    setTimeout(() => {
      bufferMessages.push(messageFromClient)
      setMessages(() => bufferMessages)
      toggleChatUpdateFlat((flag) => !flag)
    }, 1000)
  }

  const onEnterPress = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.keyCode === 13 && e.shiftKey === false) {
      e.preventDefault()
      sendMessage()
    }
  }
 /*  const messageEndRef = useRef<null | HTMLDivElement>(null); 

  useEffect(() => {
    messageEndRef.current?.scrollIntoView({ behavior: 'smooth', block: 'start' })
  },[]) */


  return (
    <div >
      <div
        style={{height: '377px',overflow:'hidden', width:'100%'}}
        data-kt-element='messages'
        
        
      >
        <ReactScrollableFeed className='d-flex  flex-column align-items-center w-100' >
        {messages.map((message, index) => {
          const userInfo = userInfos[message.user]
          const state = message.type === 'in' ? 'info' : 'primary'
          const templateAttr = {}
          if (message.template) {
            Object.defineProperty(templateAttr, 'data-kt-element', {
              value: `template-${message.type}`,
            })
          }
          const contentClass = `${''} justify-content-${
            message.type === 'in' ? 'start' : 'end'
          } mb-10`
          return (
            <div
              style={{width:'94%'}}
              key={`message${index}`}
              className={clsx('d-flex', contentClass, 'mb-10', {'d-none': message.template})}
              {...templateAttr}
            >
              <div
                className={clsx(
                  'd-flex flex-column align-items',
                  `align-items-${message.type === 'in' ? 'start' : 'end'}`
                )}
              >
                <div className='d-flex align-items-center mb-2'>
                  {message.type === 'in' ? (
                    <>
                      <div className='symbol  symbol-35px symbol-circle '>
                        <img alt='Pic' src={toAbsoluteUrl(`/media/${userInfo.avatar}`)} />
                      </div>
                      <div className='ms-3'>
                        <a
                          href='#'
                          className='fs-5 fw-bolder text-gray-900 text-hover-primary me-1'
                        >
                          {userChat.name}
                        </a>
                        <span className='text-muted fs-7 mb-1'>{message.time}</span>
                      </div>
                    </>
                  ) : (
                    <>
                      <div className='me-3'>
                        <span className='text-muted fs-7 mb-1'>{message.time}</span>
                        <a
                          href='#'
                          className='fs-5 fw-bolder text-gray-900 text-hover-primary ms-1'
                        >
                          You
                        </a>
                      </div>
                      <div className='symbol  symbol-35px symbol-circle '>
                        <img alt='Pic' src={toAbsoluteUrl(`/media/${userInfo.avatar}`)} />
                      </div>
                    </>
                  )}
                </div>

                <div
                  className={clsx(
                    'p-5 rounded',
                    `bg-light-${state}`,
                    'text-dark fw-bold mw-lg-400px',
                    `text-${message.type === 'in' ? 'start' : 'end'}`
                  )}
                  data-kt-element='message-text'
                  dangerouslySetInnerHTML={{__html: message.text}}
                ></div>
                
              </div>
              
            </div>
            
          )
          
        })}
       {/* <div ref={messageEndRef}></div> */}
        </ReactScrollableFeed >
      </div>
      <div
        className='py-4 d-flex flex-row w-100 align-items-center'
        style={{zIndex: '1', boxShadow: '0 -4px 20px 0 rgba(0, 0, 0, 0.1)'}}
      >
        <div className='ms-8'>
          <img
            alt='logochat'
            src='/media/logos/logo-chat.png'
            style={{height: '40px', width: '40px'}}
          ></img>
        </div>
        <textarea
          className='form-control form-control-flush rounded-pill '
          style={{backgroundColor: '#F4F4F4', marginLeft: '10px'}}
          rows={1}
          data-kt-element='input'
          placeholder='Type a message'
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          onKeyDown={onEnterPress}
        ></textarea>

        <div className='d-flex align-items-center me-2'>
          <button
            className='btn btn-sm btn-icon btn-active-light-primary me-1'
            type='button'
            data-bs-toggle='tooltip'
            title='Coming soon'
          >
            <i className='bi bi-paperclip fs-3'></i>
          </button>
        </div>
      </div>
    </div>
  )
}

export {ChatInner}
